
.indexList {
}
.indexList a {
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	padding: 8px 15px;
}
.imgBox {
	width: 125px;
	height: 75px;
	margin-right: 12px;
}
.imgBox img {
	width: 100%;
	height: 100%;
}
.ListMain {
	position: relative;
	-webkit-box-flex: 1;
	    -ms-flex: 1;
	        flex: 1;
}
.ListMain h2 {
	font-size: 18px;
	height: 54px;
	text-overflow: ellipsis;
}
.List_M {
	position: absolute;
	bottom: 0;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	width: 100%;
}
.List_M .lei {
	-webkit-box-flex: 1;
	    -ms-flex: 1;
	        flex: 1;
	display: block;
}
.List_M .IM_num {
	padding-left: 20px;
	background-image: url(../../assets/img/show-comment.png);
	background-size: contain;
	background-repeat: no-repeat;
}



